nav {
    display: flex;
    justify-content: space-between;
    background-color: rgb(8, 95, 59);
    align-items: center;
    padding-right: 20px;
    padding-left: 20px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif
}

.logo {
    font-size: 30px;
    color: bisque;
}
nav ul {
    list-style: none;
}
nav ul li {
    display: inline-block;
    margin-right: 8px;
}
nav ul li a {
    text-decoration: none;
    color: bisque;
}
