body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  box-sizing: border-box;
}

nav {
    display: flex;
    justify-content: space-between;
    background-color: rgb(8, 95, 59);
    align-items: center;
    padding-right: 20px;
    padding-left: 20px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif
}

.logo {
    font-size: 30px;
    color: bisque;
}
nav ul {
    list-style: none;
}
nav ul li {
    display: inline-block;
    margin-right: 8px;
}
nav ul li a {
    text-decoration: none;
    color: bisque;
}

header {
    margin: 0 auto;
    text-align: center;
}

.headerImg {
    background-image: url(/a.portfolio/static/media/moonlight.caf88d87.jpg);
    width: 100%;
    height: 100vh;
    background-repeat: no-repeat;
    background-size:cover;
}
header h1 {
    font-family: cursive;
    margin: 0;
    font-size: 4rem;
    color: rgb(59, 7, 59)
}
div.paragraph {
    display: flex;
    align-self: center;
    margin: 30px;
    padding: 10px;
    background-color: #fff;
    opacity: 0.6;
    position: absolute;
}
  div.transbox p {
    margin: 5%;
    font-weight: bold;
    color: #000000;
    font-weight: 700;
    font-size: 1.75rem;
    font-family: 'Courier New', Courier, monospace
  }

 


